import React from 'react';
import styled from 'styled-components'
import { baseUrl } from '../shared/baseUrl';
import { RenderComments } from './CommentComponent';
import { withRouter} from "react-router-dom";
import {
    Button
  } from "reactstrap";
  
import { Control } from 'react-redux-form';

class ImagenComponent extends React.Component {
    constructor() {
        super();
        this.state = {
            data: null,
            comentarios: null
        }
    }
    componentDidMount() {
        const ID = this.props.match.params.idimg;
        const bearer = "Bearer " + localStorage.getItem("token");
        return fetch(`${baseUrl}imagen/view/imagenwall/${ID}`, {
            method: "GET",
            headers: {
                Authorization: bearer
            }
        })
            .then(resp => resp.json())
            .then(img => {
                this.setState({
                    data: img
                })
            })
            .then(() => {
                return fetch(baseUrl + `comments/get-comments-image/${ID}`)
                    .then(data => data.json())
                    .then(json => {
                        this.setState({
                            comentarios: json
                        })
                    })
            })
            .catch((err) => {
                console.log(err);

            });
    }
    render() {
        const imagenes = !this.state.data ? [] : this.state.data;
        const commen = !this.state.comentarios ? [] : this.state.comentarios;
        return (
                <div>
                <Modalidad>
                  <div className="modalgroup">
                    <div className="c">
                      <div className="trashIcon">
                        {/* <Button id="trashId" onClick={(e) => toggleModal(e)}><span className=" fa fa-trash-alt "></span></Button> */}
                        <Button id="trashId" ><span className=" fa fa-trash-alt "></span></Button>

                      </div>
                      <img src={`${baseUrl}${imagenes.filename}`} alt={imagenes._id} className="resp"/>

                    </div>
                    <div className="modalComments">
                      <div className="comm">
                        {/* <RenderComments comentarios={commentarios} deleteComments={deleteComments} /> */}
                        <RenderComments comentarios={commen} />

                      </div>
                      <div className="comm-likes">{
                        // likes.length > 0
                        true
                          ?
                        //   <h6>Le gusta esta foto a <strong id="likemodal" onClick={(event) => toggleModal(event)} >{likes.length} usuarios</strong></h6>
                        <h6>Le gusta esta foto a <strong id="likemodal">usuarios</strong></h6>

                          :
                          <h6>Se el primero en marcar me gusta</h6>
                      }</div>
                      
                      <div className="foot">
                          <div className="texto">
                              <Control.textarea model=".comment" id="comment" className="textarea"/>
                          </div>
                          <div className="footButtons">
                              <div>
                              <Button><span className="fa fa-heart"></span></Button>
                                  {/* <Button onClick={(event) => this.handleLike(event)}>
                                      {
                                          !currentlyPictureId
                                              ?
                                              <span className="fa fa-heart"></span>
                                              :
                                              <span className="fa fa-heart" style={{ color: 'red' }}></span>

                                      }
                                  </Button> */}
                              </div>
                              <div className="" >
                                  <Button type="submit"><span className="fas fa-paper-plane"></span></Button>
                              </div>
                          </div>
                      </div>



                    </div>
                  </div>
                </Modalidad>
            </div>
        )
    }
}

const Modalidad = styled.div`
display: flex;
flex-direction: row;
justify-content: center;

div.modalgroup {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height:85vh;
  width:70%;
}
div.modalComments {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 32%;
  margin-left: 10px;
}
div.c {
  width: 63%;
}
div.foot {
  display: flex;
  flex-direction: row; 
}
textarea {
  width:100%;
  height: 100%;
}
div.footButtons {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.foot Button {
  // border-style: hidden;
  color: black;
  background-color: transparent;
}
div.comm {
  height: 80%;
  display: block;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

img.resp {

  width: 100%;
  height: 100%;
  max-width: auto;
  min-whidth: auto;
  object-fit: contain;
}
img.comment{
  margin-left: 5%;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
}
@media (max-width: 747px) {
  div.modalgroup {
    display: inline-block;
    width: 100%;
    margin-top: -21px;
  }
  div.modalComments {
    display: flex;
    justify-content: stretch;
    width: 100%;
    margin: 0px;
  }
  div.comm, div.comm-likes  {
    display: none;
  }
  div.foot{
    display: flex;
    justify-content:stretch;
  }
  .texto {
    width:100%;
  }
  div.c {
    display:inline-block
    width: 100%;
  }
  img.resp {
    width: 100%;
    object-fit: fit;
  }
}
}
`

